<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <nav-head :title="title" ></nav-head>
      </div>
      <!--      导航-->
      <!--      header 头部-->
      <div class="header">
        <div class="dividing-line"></div>
      </div>
      <!--内容-->
      <div class="main">
        <!-- 标题 -->
        <div class="headTitle">
          <div class="info clear-fix">
            <img class="img fl" :src=" url+ datas.pic" alt="">
            <span class="fl coin">{{datas.currency_id}}</span>
            <span class="fl coins">({{datas.title}})</span>
            <span class="fl types" @click="showVent()">{{datas.start_time > setTime ? '未开始' : setTime > datas.start_time && datas.end_time > setTime ? '进行中' : '已结束' }}</span>
          </div>
          <div class="proportion">
            <div class="left">兑换比例 : {{datas.price}}{{datas.x_currency_id}} ≈ 1 {{datas.currency_id}}</div>
            <div class="right">结束时间： {{format(datas.end_time)}}</div>
          </div>
        </div>
         <div class="describeBox">
            <div class="describeTitle">
              <div class="title">
                <p class="tip">发行额度</p>
                <p class="num">{{datas.total_num}}</p>
              </div>
              <div class="title">
                <p class="tip">已参与量</p>
                <p class="num">{{datas.deal}}</p>
              </div>
              <div class="title">
                <p class="tip">锁仓周期</p>
                <p class="num">{{datas.unfreeze}}期</p>
              </div>
              <div class="title">
                <p class="tip">解冻间隔</p>
                <p class="num">{{datas.financial_cycle}}{{datas.time_type}}</p>
              </div>
            </div>
            <div class="process-bar" style="flex: 1 1 0%;">
              <!-- style="width: 7.64%;" -->
              <div  class="process-bar-hover"  :style="{ width: ( Number(datas.deal) / Number(datas.total_num) *100)+'%'}" ></div>
            </div>
            <div class="proportion">
              <div class="left"> <span style="color: #20C981;">{{datas.supporters}}</span> 支持者</div>
              <!-- ( Number(item.deal) / Number(item.total_num) *100) -->
              <div class="right">{{Number(datas.deal) / Number(datas.total_num) *100}}%</div>
            </div>
          </div>
          <!-- 文本部分 -->
          <div class="boxText" v-html="datas.content">

          </div>
          <div class="tips">
            <div class="title">温馨提示：</div>
            <p class="text">
              数字资产是创新的投资产品，可能存在矿工预挖、庄家操控、团队解散、技术缺陷等问题；其价格波动较大，具有较高的投资风险，请您投资前对数字资产充分认知，理性判断自己的投资能力，审慎做出投资决策。
            </p>
          </div>
      </div>
      <!-- 弹窗 -->
      <!-- v-if="isShow" -->
      <div class="posiBox"  v-if="isShow" @click="showBox()"></div>
      <div class="ventanal"  v-if="isShow">
        <div class="title">立即参与</div>
        <div class="isTips">
          <span class="num">余额: <span style="color:#3DD078">{{datas.balance}}{{datas.x_currency_id}}</span></span>
          <span class="nums">单笔限购：<span style="color:#FA4C45">{{datas.max}}{{datas.currency_id}}</span></span>
        </div>
        <div class="iptBox">
          <span class="text">购买数量</span>
          <input class="ipt" type="number" v-model="addCoin.num" placeholder="请输入数量">
        </div>
        <div class="iptBox">
          <span class="text">交易密码</span>
          <input class="ipt" type="password" v-model="addCoin.paypass" placeholder="请输入交易密码">
        </div>
        <div class="subBox" @click="userSub()">提交</div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Toast } from 'vant'
import { newcoindetails, addnewcoin } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
Vue.use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      url: this.GLOBAL.domainName, // 公共参数
      // 请求详情数据参数
      parameter: {
        u_id: '2',
        activity: 'newcoin',
        activity_id: ''
      },
      datas: [],
      curHeight: '',
      setTime: '',
      // 参与活动参数
      addCoin: {
        u_id: '2', // 用户ID
        activity: 'newcoin', // 固定值（newcoin）
        activity_id: '', // 活动ID
        num: '', // 购买数量
        paypass: '' // 交易密码
      },
      isShow: false, // 判断活动弹窗判断
      title: '详情'
    }
  },
  created () {
    this.parameter.u_id = sessionStorage.getItem('u_id')
    this.addCoin.u_id = sessionStorage.getItem('u_id')
    var timestamp3 = Date.parse(new Date())
    this.setTime = timestamp3 / 1000
    this.parameter.activity_id = this.$route.query.id
    this.addCoin.activity_id = this.$route.query.id
    var data = objKeySort(this.parameter)
    var token = sgin(data)
    this.parameter.token = token
    this.coinList(this.parameter)
    this.beforeMount()
  },
  methods: {
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    coinList (parameter) {
      var that = this
      newcoindetails(parameter).then(res => {
        that.datas = res.data
      }).catch((err) => console.log(err))
    },
    add0 (m) { return m < 10 ? '0' + m : m },
    format (isTime) {
      var times = isTime * 1000
      var time = new Date(times)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      // var h = time.getHours()
      // var mm = time.getMinutes()
      // var s = time.getSeconds()
      // console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
      return y + '-' + this.add0(m) + '-' + this.add0(d)
    },
    userSub () {
      var that = this
      if (this.addCoin.num === '') {
        that.$toast({
          message: '购买数量不能为空',
          duration: 1500
        })
        return
      }
      if (Number(this.datas.min) > Number(this.addCoin.num)) {
        var msg = '购买数量不能少于' + parseInt(this.datas.min)
        that.$toast({
          message: msg,
          duration: 1500
        })
        return
      }
      if (this.addCoin.paypass === '') {
        that.$toast({
          message: '交易密码不能为空',
          duration: 1500
        })
        return
      }
      var data = objKeySort(this.addCoin)
      var token = sgin(data)
      this.addCoin.token = token
      addnewcoin(this.addCoin).then(res => {
        console.log(res, that, '交易成功')
        if (res.code === 0) {
          that.$toast({
            message: '参与成功',
            duration: 1500
          })
          that.isShow = false
          that.addCoin.num = ''
          that.addCoin.paypass = ''
          delete this.addCoin.token
        }
        // this.datas = res.data
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        that.isShow = false
        that.addCoin.num = ''
        that.addCoin.paypass = ''
        delete this.addCoin.token
      })
    },
    showBox () {
      this.isShow = false
    },
    showVent () {
      var that = this
      if (this.datas.start_time > this.setTime) {
        that.$toast({
          message: '活动未开始',
          duration: 1500
        })
      } else if (this.setTime > this.datas.start_time && this.setTime < this.datas.end_time) {
        that.isShow = true
      } else {
        that.$toast({
          message: '活动已结束',
          duration: 1500
        })
      }
    }
  }
}
</script>
<style scoped>
  .film-box{
    position: relative;
  }
  .content{
    color: white;
    font-size: .28rem;
    background: #000;
  }
  .nav-box{
    height: 1.28rem;
    line-height: 1.28rem;
    display: flex;
    justify-content: space-between;
    padding: 0 0.3rem;
    padding-bottom: 0rem;
    color: white;
    font-size: 0.36rem;
    background: #202641;
  }
  .nav-box img{
    vertical-align: middle;
    flex: 1;
  }
  .dividing-line{
    width: 100%;
    height: 0.2rem;
    background-color: #000;
  }
  .headTitle{
    padding: 0.26rem 0.3rem 0.25rem 0.3rem;
    background: #202641;
    border-bottom: 0.01rem solid #000;
  }
  .headTitle .info{
    position: relative;
    line-height: 0.7rem;
  }
  .headTitle .info .img{
    width: 0.7rem;
    height: 0.7rem;
    border-radius: 100%;
    margin-right: 0.36rem;
  }
  .headTitle .info .coin{
    color: #ffffff;
    opacity: 0.7;
    font-size: 0.32rem;
    margin-right: 0.25rem;
  }
  .headTitle .info .coins{
    color: #ffffff;
    opacity: 0.5;
    font-size: 0.24rem;
  }
  .headTitle .info .types{
    position: absolute;
    right: 0.3rem;
    height: 0.5rem;
    top: 0.1rem;
    line-height: 0.5rem;
    padding: 0rem 0.2rem;
    color: #FA4C45;
    background:linear-gradient(0deg,rgba(250,76,69,0.2),rgba(222,61,102,0.2));
    box-shadow:0px 0px 6px 0px rgba(250,76,69,0.5);
  }
  .headTitle .proportion{
    display: flex;
    margin-top: 0.26rem;
  }
  .headTitle .proportion .left{
    flex: 1;
    text-align: left;
    color:rgba(255,255,255,0.6);
    font-size: 0.24rem;
  }
  .headTitle .proportion .right{
    text-align: right;
    color:rgba(255,255,255,0.3);
    font-size: 0.24rem;
  }
  .describeBox {
     padding: 0rem 0.3rem 0rem 0.3rem;
     background: #191E35;
     margin-bottom: 0.02rem solid #000;
  }
  .describeBox .describeTitle{
    display: flex;
    padding: 0.6rem 0rem 0.75rem 0rem;
    text-align: center;
  }
  .describeBox .describeTitle .title{
    flex: 1;
  }
  .describeBox .describeTitle .title .tip{
    font-size: 0.26rem;
    color: rgba(255,255,255,0.7);
    margin-bottom: 0.23rem;
  }
  .describeBox .describeTitle .title .num{
    font-size: 0.28rem;
    color: rgba(255,255,255,0.5);
  }
  /* 进度条 */
  .process-bar{
    /* margin: 0rem 0.6rem 0 0.54rem; */
    width: 100%;
    height: 0.05rem;
    background-color: #EE4553;
    border-radius: 0.1rem;
  }
  .process-bar .process-bar-hover {
    border-radius: 0.1rem;
    height: 0.05rem;
    /* background: linear-gradient(to right, #1fb6c7, #02d5a8); */
    background:linear-gradient(0deg,rgba(57,207,118,1),rgba(10,195,138,1));
    transition: width 3s;
    /* box-shadow: 1px 1px 5px rgb(0, 226, 181), -1px 1px 5px rgb(0, 226, 181), -1px -1px 5px rgb(0, 226, 181), 1px -1px 5px rgb(0, 226, 181); */
  }
  .describeBox .proportion{
    display: flex;
    margin-top: 0.26rem;
    padding-bottom: 0.33rem;
  }
  .describeBox .proportion .left{
    flex: 1;
    text-align: left;
    color:rgba(255,255,255,0.6);
    font-size: 0.28rem;
  }
  .describeBox .proportion .right{
    flex: 1;
    text-align: right;
    color:#20C981;
    font-size: 0.28rem;
  }
  .boxText{
    background: #191E35;
    min-height: 3rem;
    margin-top: 0.2rem;
    border-bottom: 0.01rem solid #000;
    padding: 0.3rem 0.3rem;
  }
  .boxText >>> *{
    text-indent: 0.25rem !important;
    text-align: left !important;
    color: rgba(255,255,255,0.7) !important;
    line-height: 0.5rem;
  }
  .tips{
    padding: 0.37rem 0.3rem 0.3rem 0.3rem;
    background: #12172B;
  }
  .tips .title{
    color: #EE4845;
    font-size: 0.28rem;
    font-family:Source Han Sans CN;
    text-align: left;
    margin-bottom: 0.23rem;
  }
  .tips .text{
    font-size: 0.24rem;
    text-indent: 0.25rem;
    text-align: left;
    color: rgba(255,255,255,0.5);
  }
  .posiBox{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background: #000;
    opacity: 0.8;
    z-index: 10;
  }
  .ventanal{
    width: 5.5rem;
    height: 5.5rem;
    background: #1B213B;
    position: fixed;
    left: 50%;
    top: 50%;
    /* margin-left: -2.75rem; */
    margin-left: -3rem;
