    padding: 0.25rem 0.25rem;
    z-index: 11;
    border-radius: 0.2rem;
  }
  .ventanal .title{
    color:rgba(218,218,221,1);
    line-height:43px;
    background:linear-gradient(0deg,rgba(16,99,255,1) 0%, rgba(2,197,255,1) 100%);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;
    font-size: 0.36rem;
  }
  .ventanal .isTips{
    display: flex;
  }
  .ventanal .isTips .num{
    flex: 1;
    text-align: left;
    color: rgba(255,255,255,0.6);
    font-size: 0.24rem;
  }
  .ventanal .isTips .nums{
    flex: 1;
    text-align: right;
    color: rgba(255,255,255,0.3);
    font-size: 0.24rem;
  }
  .ventanal .iptBox{
    /* display: flex;
    padding: 0.2rem 0; */
    flex: 1;
    background: rgba(18,23,43,1);
    border: none;
    padding: 0.2rem 0.2rem;
    margin-top: 0.5rem;
  }
  .ventanal .iptBox .text{
    font-size: 0.15rem;
    color: rgba(255,255,255,0.7);
    margin-right: 0.2rem;
    display: inline-block;
    line-height: 0.5rem;
  }
  .ventanal .iptBox .ipt{
    flex: 1;
    background: rgba(18,23,43,1);
    border: none;
    padding: 0.1rem 0.2rem;
  }
  .ventanal .subBox{
    background:linear-gradient(-90deg,rgba(16,99,255,1),rgba(2,197,255,1));
    border-radius:0.1rem;
    height: 0.7rem;
    line-height: 0.7rem;
    color: rgba(255,255,255,1);
    text-align: center;
    font-size: 0.28rem;
    margin-top: 0.5rem;
  }
</style>
